<template>
  <v-container>
    <!-- animation on percentage -->
    <div>
      <h1 class="text-center mx-auto my-3 hidden-md-and-up">Study Tracker</h1>
    </div>

    <div class="text-center mt-2">
      <div class="progress_container">
        <v-progress-circular
          color="indigo"
          :size="150"
          :width="15"
          :value="totalper.toFixed(2)"
          striped
          class="mx-auto"
        >
          <strong>{{ totalper.toFixed(2) }}%</strong>
        </v-progress-circular>
        <h3 class="text-center mx-auto my-3">Total Progress</h3>
      </div>
    </div>

    <v-card class="transparent mt-3" flat>
      <div class="subject__filters my-3">
        <button
          :class="`subject__button ${se == '' && 'activebtn'}`"
          @click="selectSubject('')"
        >
          All
        </button>
        <button
          v-for="(s, i) in subjects"
          :key="i"
          :class="`subject__button ${se == s && 'activebtn'}`"
          @click="selectSubject(s)"
        >
          {{ s }}
        </button>
      </div>
      <v-card-text>
        <v-row>
          <v-col v-for="(c, i) in checklists" :key="i" cols="12">
            <div class="text-center my-3 m_contain">
              <h2 class="kalpurush mt-2">{{ c.Chapters }}</h2>
              <h3 class="kalpurush my-4">{{ c.Subject }}</h3>

              <div>
                <v-progress-circular
                  :width="10"
                  :value="c.progressper"
                  :size="85"
                  color="#6f5fdb"
                  class="mx-auto"
                >
                  <h3>{{ c.progressper }}%</h3>
                </v-progress-circular>
              </div>
              <div class="m_btns">
                <v-btn
                  color="#6f5fdb"
                  dark
                  v-if="c.playlist"
                  :href="c.playlist"
                  target="_blank"
                >
                  <v-icon>mdi-play</v-icon> Playlist
                </v-btn>
                <v-btn color="#6f5fdb" @click="openForEdit(c)" dark>
                  <v-icon>mdi-circle-edit-outline</v-icon> Update Progress
                </v-btn>
                <v-btn
                  color="#6f5fdb"
                  dark
                  @click="$swal({ icon: 'warning', text: 'Upcoming' })"
                >
                  <v-icon>mdi-head-question</v-icon> Quiz
                </v-btn>
              </div>
            </div>
          </v-col>
        </v-row>
        <div v-if="checklists.length == 0" class="text-center">
          <v-progress-circular
            :width="10"
            indeterminate
            :size="100"
            color="#6f5fdb"
            class="mx-auto"
          >
          </v-progress-circular>
        </div>
      </v-card-text>
    </v-card>
    <v-dialog v-model="checkitem" max-width="600">
      <v-card>
        <v-card-title>
          <h2 class="mx-auto white--text text-center indigo pa-2 mt-2 det">
            Check List Details
          </h2>
        </v-card-title>
        <v-card-text>
          <v-list>
            <v-list-item class="mt-1 boxed">
              <v-list-item-content class="text-center">
                <div class="row justify-center items-center align-center">
                  <h3 class="text-center">Video</h3>
                  <v-checkbox
                    color="indigo"
                    class="ml-3"
                    v-model="selected.video"
                  />
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-list-item class="mt-5 boxed">
              <v-list-item-content class="text-center">
                <div class="row justify-center items-center align-center">
                  <h3 class="text-center">Book</h3>
                  <v-checkbox
                    color="indigo"
                    class="ml-3"
                    v-model="selected.book"
                  />
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-list-item class="mt-5 boxed">
              <v-list-item-content class="text-center">
                <div class="row justify-center items-center align-center">
                  <h3 class="text-center">Note</h3>
                  <v-checkbox
                    color="indigo"
                    class="ml-3"
                    v-model="selected.note"
                  />
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-list-item class="mt-5 boxed">
              <v-list-item-content class="text-center">
                <div class="row justify-center items-center align-center">
                  <h3 class="text-center">Test Paper</h3>
                  <v-checkbox
                    color="indigo"
                    class="ml-3"
                    v-model="selected.testPaper"
                  />
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-list-item class="mt-5 boxed">
              <v-list-item-content>
                <div class="row justify-center items-center align-center">
                  <h3 class="text-center">Question Bank</h3>
                  <v-checkbox
                    color="indigo"
                    class="ml-3"
                    v-model="selected.testPaper"
                  />
                </div>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="orange" dark tile large @click="checkitem = false"
            >Cancel</v-btn
          >
          <v-btn
            class="indigo"
            tile
            large
            dark
            @click="updateitem"
            :loading="loading"
            >Update</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      checklists: [],
      allchecklists: [],
      checkitem: false,

      selected: {},
      loading: false,
      totalper: 0,
      se: "",
      subjects: new Set(),
    };
  },
  methods: {
    updateitem() {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbzesOKX8-ma3N26RfY3vvMoeeORSk1eX2RlKaUJsPkzR3vzcfDavZDgwZtLA77ihmi1Qg/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            sheetid: this.user.photoURL,
            ...this.selected,
            type: "progress",
          }),
        }
      ).then(() => {
        let index = this.checklists.findIndex((s) => s.id == this.selected.id);

        this.checklists[index] = { ...this.selected };
        this.loading = false;
        this.getChecklists();
        this.checkitem = false;
      });
    },
    openForEdit(item) {
      this.selected = item;
      this.checkitem = true;
    },
    getChecklists() {
      fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${this.user.photoURL}&type=progress`
      )
        .then((res) => res.json())
        .then((data) => {
          fetch(
            "https://script.google.com/macros/s/AKfycbym77m6CPMbha4InMiUZDoV6fMEC2ili7jX-iDVWh_vS0De8Hh0wHVM9bvHiB6LFWIy/exec"
          )
            .then((r) => r.json())
            .then((k) => {
              let pers = 0;
              this.allchecklists = data.routines.map((d) => {
                let check = k.routine.find((m) => d.id == m.id);
                if (d.Subject) {
                  let s = d.Subject.split(/\d/)[0];
                  this.subjects.add(s);
                }
                let c = this.getProgress(d);
                pers += c["progressper"];
                c.playlist = check?.playlist;
                return c;
              });

              this.allchecklists.length = 75;
              this.checklists = this.allchecklists;
              this.totalper = pers / this.checklists.length;
            });
        });
    },
    getProgress(data) {
      let count = 0;
      for (const k in data) {
        if (["video", "book", "note", "qbank", "testPaper"].includes(k)) {
          if (data[k]) {
            count++;
          }
        }
      }

      switch (count) {
        case 1:
          data["progress"] = "More to Go";
          break;
        case 2:
          data["progress"] = "Study Hard";
          break;
        case 3:
          data["progress"] = "Keep it up";
          break;
        case 4:
          data["progress"] = "Almost There";
          break;
        case 5:
          data["progress"] = "Chapter Complete";
          break;
        default:
          data["progress"] = "No progress";
      }

      data["progressper"] = count * 20;
      return data;
    },
    selectSubject(s) {
      this.se = s;
      this.checklists = this.allchecklists.filter((r) => {
        if (r.Subject) {
          if (r.Subject.toLowerCase().includes(s.toLowerCase())) {
            return r;
          }
        }
      });
    },
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
  created() {
    this.getChecklists();
  },
};
</script>

